
import requests from "./request";


export const getData = () => {
    return requests.get('/home/getData');
}


export const getUser = (params) => {
    return requests.get('/user/getUser', params);
}

export const addUser = (data) => {
    return requests.post('/user/add', data);
}

export const editUser = (data) => {
    return requests.post('/user/edit', data);
}

export const delUser = (data) => {
    return requests.post('/user/delete', data);
}




export const getMenu = (data) => {
    return requests.post('/permission/getMenu', data);
}