//对axios进行二次封装
import axios from "axios";


const requests = axios.create({
    baseURL: '/api',//通用请求地址
    timeout: 5000,
});

//引入进度条功能 当请求发出时 出现进度条 请求响应结束时进度条结束
import nprogress from 'nprogress';
//start 方法代表开始
//done 方法代表结束
import 'nprogress/nprogress.css'

//请求拦截器:在请求发送之前 可以检测到 在发送前做一些事情 
requests.interceptors.request.use((config) => {
    //config是配置对象 里面有一个属性headers
    nprogress.start();
    return config;
});


//响应拦截器
requests.interceptors.response.use((res) => {
    //服务器成功回调函数
    nprogress.done();
    return res.data;
}, (err) => {
    //服务器失败回调函数
    return Promise.reject(new Error('faile'));
});

//对外暴露二次封装的axios
export default requests;